body {
  background-color: #f9f9f9;
  font-family: "Crimson Text";
}

.main {
  background: #fff;
}

.home-description {
  font-style: italic;
  font-weight: 600;
  font-family: "Crimson";
  font-size: 19px;
  padding-top: 15px;
  padding-bottom: 25px;
  line-height: 29px;
}

.social a .svg {
  height: 40px;
  width: 40px;
  border: 1px solid #111;
  line-height: 40px;
  margin: 0 10px;
  color: #111;
  font-size: 20px;
  padding: 10px;
}

.social a:hover .svg {
  color: #fff;
  border-color: #bb9e7d;
  background-color: #bb9e7d;
}

@media (min-width: 778px) {
  .main {
    height: 100vh;
    overflow: hidden;
  }

  .btn-group-vertical {
    padding: 20px 87.5px;
  }

  .info {
    font-family: "Crimson";
    line-height: 28px;
    font-size: 16px;
    padding-top: 15px;
    padding-right: 50px;
  }

  .home-description {
    padding-right: 90px;
    padding-left: 90px;
  }
}
.img-rabbit {
  width: 100%;
}
.btn-rabbit {
  background-color: #fff;
  color: #111;
  font-style: italic;
  border: 1px solid #111;
  border-radius: 0px !important;
  transition: all 0.4s ease-in-out;
  margin-bottom: 20px;
  font-family: "Crimson";
  font-size: 13px;
}

.btn-rabbit:hover {
  color: #fff;
  background-color: #bb9e7d;
  border-color: #bb9e7d;
}

p {
  color: #585757;
  font-size: 16px;
}

.page-title {
  text-transform: uppercase;
  font-family: "Josefin Sans";
}

/*Home Style*/
.logo {
  padding-top: 100px;
  text-transform: uppercase;
  font-size: 12px;
}

.logo h4 {
  font-family: "Josefin Sans";
}

footer {
  padding: 22px 0 10px 0;
  font-style: italic;
}

.back {
  width: 170px;
  font-family: "Crimson";
  font-size: 17px;
  font-style: italic;
  text-align: center;
  display: block;
  clear: both;
  margin-top: 70px;
}

.back svg {
  margin-left: -40px;
  margin-right: 17px;
  font-size: 20px;
}

.form-control {
  margin-top: 30px;
  width: 75%;
}

.btn {
  padding: 10px 60px;
  height: 45px;
  font-size: 16px;
}

.btn-group-vertical {
  font-family: "Josefin Sans";
}

.about {
  font-family: "Josefin Sans";
  font-size: 32px;
}
.form-control {
  font-style: italic;
  border-radius: 0;
  font-family: "Crimson";
  font-size: 16px;
}

.page-title {
  font-size: 32px;
  font-family: "Josefin Sans";
}

.fa-heartbeat {
  color: #bb9e7d;
}

::selection {
  color: #fff;
  background-color: #bb9e7d;
}

.bottom {
  padding-right: 20px;
}

.page-title {
  position: relative;
  display: inline-block;
}

.page-title:after,
.page-title:before {
  content: "";
  position: absolute;
  height: 1px;
  background-color: black;
  left: 0;
  margin: 2px;
}

.social {
  padding-bottom: 15px;
}

.submit {
  font-family: "Crimson";
  font-size: 16px;
}

.subtitle {
  font-style: italic;
  font-weight: 600;
  font-size: 20px;
  color: #3b3b3b;
  line-height: 28px;
  font-family: "Crimson";
  margin-top: 15px;
  margin-bottom: 20px;
  padding-right: 40px;
}
.page-title:after {
  width: 75%;
  top: -9px;
}

.page-title:before {
  width: 40%;
  bottom: -7px;
}

#watermark .marker {
  position: absolute;
  font-size: 60px;
  opacity: 0.1;
  font-family: "Josefin Sans";
  text-transform: uppercase;
  left: 0;
  top: -5px;
}

#watermark {
  position: relative;
}

.item {
  background-color: #313131;
  color: #f9f9f9;
}

#owl-demo .item img {
  display: block;
  width: 100%;
  height: auto;
  /*margin-left: 0px;*/
}

.owl-theme .owl-controls {
  margin-top: 10px;
  margin-right: 10px;
  text-align: center;
  width: 100%;
  position: absolute;
  top: calc(44% - 40px);
}

.owl-next {
  right: 0;
  padding-right: 1px;
}

.owl-prev {
  left: 0;
  padding-left: 1px;
}

.owl-theme .owl-controls .owl-nav [class*="owl-"] {
  position: absolute;
  border-radius: 0px;
  font-size: 21px;
  padding: 15px 20px;
  margin: 0px;
  background-color: #fff;
}

.owl-theme .owl-controls .owl-nav [class*="owl-"] i {
  color: #333;
}

.owl-theme .owl-controls .owl-nav [class*="owl-"]:hover {
  background-color: #bb9e7d;
  border-color: #bb9e7d;
}
.owl-theme .owl-controls .owl-nav [class*="owl-"]:hover i {
  color: #fff;
}
@media (max-width: 1024px) and (orientation: landscape) {
  .main {
    height: auto;
  }
}
.custom_btn {
  -vendor-animation-duration: 3s;
  -vendor-animation-delay: 2s;
  -vendor-animation-iteration-count: infinite;
}
.show {
  display: block;
}
